@import url(https://fonts.googleapis.com/css2?family=Raleway:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&display=swap);
/* Imports */

/* Variables */

:root {
    --ff-main: Raleway, sans;
    --clr-black: #000;
    --clr-white: #fff;
    --clr-main: #7403FF;

    --dft-padding: .5rem;
    --fs-default: 16px;
}

/* Reset */

*,
*::after,
*::before {
    margin: 0;
    padding: 0;
    box-sizing: inherit;
}

html {
    font-size: 16px;
    font-size: var(--fs-default);
    box-sizing: border-box;
    scroll-behavior: smooth;
}

body {
    max-width: 100vw;
}

img {
    display: block;
}


/* Featured Blog Item */

.featured__blog-item {
  padding: var(--dft-padding);
  height: 100%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  grid-gap: 1rem;
  gap: 1rem;
  color: var(--clr-white);
}

.featured__blog-item h1 {
  text-align: center;
  font-size: 3rem;
}

.featured__blog-item p {
  text-align: justify;
  font-size: 1.1rem;
  line-height: 1.3;
  margin: 1em 0;
  padding: 1rem;
}

.featured__blog-item button {
  -webkit-align-self: center;
          align-self: center;

  font-size: 1.2rem;
  color: var(--clr-white);
  background-color: var(--clr-main);
  border: 0;
  border-radius: 5px;
  width: 150px;
  height: 70px;
}

@media only screen and (min-width: 900px) {
  .featured__blog-item {
      -webkit-align-items: flex-start;
              align-items: flex-start;
      padding-left: 5rem;
  }
  .featured__blog-item h1 {
      font-size: 2.5rem;
  }
  .featured__blog-item p {
      text-align: justify;
      width: 40%;
      padding: 0;
  }
  .featured__blog-item button {
      -webkit-align-self: flex-start;
              align-self: flex-start;
  }
}


header {
    width: 100%;
    height: 100vh;
    background: linear-gradient(rgba(0,0,0,0.4), rgba(0,0,0,0.4)), url("https://picsum.photos/4000") center/cover fixed;
}

/* Nav Bar */

/* Need to remove later */

.nav__bg-enable {
    background-color: rgba(0,0,0,0.4);
}

.nav__bg-disable {
    background-color: rgba(0,0,0,0.0);
}

#nav-bg {
    padding: 0 2rem;
}

.nav__toggle-menu {
    display: none;
}

.nav__toggle-menu {
    background-color: inherit;
}

.nav__bar {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    padding: 1rem;
}

.nav__menu-icon {
    font-size: 2rem;
    color: var(--clr-white);
}

.nav__toggle-menu {
    width: 100%;
    position: absolute;
    padding: .5rem;
    text-align: right;
}

.nav__toggle-menu li {
    list-style: none;
    line-height: 1.6;
}

.nav__toggle-menu a {
    text-decoration: none;
    color: var(--clr-white);
    font-size: 1.4rem;
    cursor: pointer;
}

.active {
    border-bottom: 2px solid var(--clr-white);
    padding-bottom: 2px;
}

@media only screen and (min-width: 900px) {
    .nav__menu-icon {
        display: none;
    }
    #nav-bg {
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-between;
                justify-content: space-between;
    }
    .nav__toggle-menu {
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-align-items: center;
                align-items: center;
        position: relative;
        width: inherit;
    }
    .nav__toggle-menu li {
        display: inline-block;
        margin-right: 1rem;
    }
}

.logo a {
    font-size: 1.5rem;
    text-decoration: none;
    color: var(--clr-white);
}

.logo span {
    color: var(--clr-main);
}

.blog__item > img {
    width: 100%;
}

.blog__item {
    padding: 1rem;
}

.blog__item {
    padding: 1rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
}

.blog__item-content {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
}

.blog__item-content h1 {
    font-size: 1.8rem;
    text-align: left;
}

.blog__item-content p {
    font-size: 1.2rem;
    text-align: left;
    line-height: 1.3;
    margin-bottom: .5rem;
}

.blog__item-content button {

    font-size: 1.1rem;
    font-weight: 400;
    color: var(--clr-white);
    background-color: var(--clr-main);
    border: 0;
    border-radius: 5px;
    width: 150px;
    height: 70px;
}

@media only screen and (min-width: 900px) {
    .blog__item-img {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }
}

/* Blog Items */

.blog-items {
    padding: 1rem;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
    margin-bottom: 1rem;
}

.blog-items > h1 {
    text-align: center;
    color: var(--clr-main);
    font-size: 2rem;
    margin-bottom: 50px;
    border-bottom: 2px solid var(--clr-main);
    padding: 1rem;
}


@media only screen and (min-width: 900px) {
    .blog-items > h1 {
        border: none;
        display: -webkit-flex;
        display: flex;
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-align-items: center;
                align-items: center;
        grid-column: span 2;
    }
    .blog-items > h1::after {
        content: '';
        background-color: var(--clr-main);
        display: block;
        width: 150px;
        height: 2px;
        margin-top: 15px;
    }
    .blog-items {
        display: grid;
        grid-template-columns: 1fr 1fr;
        grid-template-rows: auto;
    }
}


/* Footer */

footer {
    background-color: var(--clr-black);
    color: var(--clr-white);
    display: -webkit-flex;
    display: flex;
    height: 100px;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

footer p {
    font-size: 1.4rem;
}

